@charset "UTF-8";
/* CSS reset*/
html, body, div, span, h1, h2, h3, h4, h5, h6, p, a, dl, dt, dd, ul, ol, li, nav, footer, header,
form, input, optgroup, select, textarea, video, audio, iframe, button, pre, code, blockquote, q,
fieldset, legend, caption, object, article, aside, details, figcaption, figure, menu, section {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
}
body, button, input, optgroup, select, textarea {
  font: 14px/1.4286 "microsoft yahei", simsun, tahoma, arial, sans-serif;
}
h1, h2, h3, h4, h5, h6 {
  font-size: 100%;
  font-weight: 500;
}
a, ins {
  text-decoration: none;
}
a {
  background-color: transparent;
}
a:active, a:hover {
  outline-width: 0;
}
audio, canvas, video, progress {
  display: inline-block;
  *display: inline;
  *zoom: 1;
}
article, aside, details, figcaption, figure, footer, header, menu, nav, section, iframe, main {
  display: block;
}
summary {
  display: list-item;
}
template, [hidden] {
  display: none;
}
svg:not(:root) {
  overflow: hidden;
}
abbr, acronym {
  border: 0;
  font-variant: normal;
}
del {
  text-decoration: line-through;
}
caption, th {
  text-align: left;
}
mark {
  background-color: #ff0;
  color: #000;
}
small {
  font-size: 80%;
}
sub, sup {
  font-size: 75%;
  line-height: 0;
  position: relative;
  vertical-align: baseline;
}
sup {
  top: -0.5em;
}
sub {
  bottom: -0.25em;
}
img {
  vertical-align: middle;
  border-style: none;
}
ol, ul {
  list-style: none;
}
table {
  border-collapse: collapse;
  border-spacing: 0;
}
table, tbody, tfoot, thead, tr, th, td {
  padding: 0;
}
address, caption, cite, code, dfn, th, var, em, i, time {
  font-style: normal;
  font-weight: 500;
}
b, strong {
  font-weight: inherit;
  font-weight: bolder;
}
blockquote, q {
  quotes: none;
}
blockquote:before, blockquote:after, q:before, q:after {
  content: "";
  content: none;
}
hr {
  display: block;
  height: 1px;
  border: 0;
  border-top: 1px solid #ccc;
  margin: 1em 0;
  padding: 0;
}
button, input, select, textarea, :focus {
  outline: none;
}
textarea {
  overflow: auto;
}
button, input {
  overflow: visible;
}
button, select {
  text-transform: none;
}
button::-moz-focus-inner, [type="button"]::-moz-focus-inner, [type="reset"]::-moz-focus-inner, [type="submit"]::-moz-focus-inner {
  border-style: none;
  padding: 0;
}
button:-moz-focusring, [type="button"]:-moz-focusring, [type="reset"]:-moz-focusring, [type="submit"]:-moz-focusring {
  outline: 1px dotted ButtonText;
}
[type="number"]::-webkit-inner-spin-button, [type="number"]::-webkit-outer-spin-button {
  height: auto;
}
[type="search"] {
  -webkit-appearance: textfield;
  outline-offset: -2px;
}
[type="search"]::-webkit-search-cancel-button, [type="search"]::-webkit-search-decoration {
  -webkit-appearance: none;
}
body {
  word-wrap: break-word;
}

/* 全局*/
html, body {
  height: 100%;
}
html {
  _overflow-x: hidden;
}
body {
  background-color: #009872;
  color: #333;
  min-width: 1000px;
  position: relative;
}
a {
  color: #333;
}
a:hover {
  text-decoration: none;
}
/*/全局*/

.image_list {
  overflow: hidden;
  list-style: none;
  background: url(../images/grid.jpg);

  li {
    margin: 10px;
    color: #fff;
    overflow: hidden;

    div {

      &:first-child {
        display: none;
      }

      &:last-child {
        img {
          max-width: 100%;
        }
      }
    }
    dt,
    dd {
      float: left;
      line-height: 24px;
      font-size: 12px;
    }
    dt {
      clear: left;
    }
  }
}
.wrap {
  position: absolute;
  width: 814px;
  left: 50%;
  top: 100px;
  min-height: 224px;
  margin-left: -400px;
  border: 5px dashed #007255;
  text-align: center;
  background: #fff;
  color: #135042;
  font-size: 24px;
}
.dropbox {
  line-height: 0;
  font-size: 0;
  background: #f7f7f7;
  padding: 60px 0;

  dl {
    padding: 20px 0;

    dt {
      height: 64px;
      width: 64px;
      background: url(../images/plus.png);
      display: inline-block;
      opacity: .5;
      margin-right: 10px;
    }
    dd {
      font-size: 24px;
      line-height: 64px;
      display: inline-block;
      height: 64px;
      vertical-align: top;
    }
  }
}
.settings {
  line-height: 40px;
  margin: 30px 0;

  label {
    margin-right: 40px
  }
  input[type='text'] {
    height: 30px;
    line-height: 30px;
    font-size: 16px;
    color: #666;
    text-align: center;
    border: 1px solid #000;
  }
  select {
    color: #fff;
    font-size: 16px;
    height: 30px;
    line-height: 30px;
    background: #00906c;
    border-radius: 5px;
    padding: 0 5px 0 10px
  }
  .submit_btn {
    color: #fff;
    background: #00906c;
    padding: 0 20px;
    border: 0;
    height: 40px;
    line-height: 40px;
    font-size: 16px;
    border-radius: 5px;
    box-shadow: 1px 1px 1px rgba(0, 0, 0, .3);
    margin-left: 15px;
  }

}
.result_pop {
  position: absolute;
  top: 0;
  right: 0;
  left: 0;
  bottom: 0;
  overflow: auto;
  overflow-x: hidden;
  padding: 50px 200px 50px 50px;
  background: #00aecc;

  textarea {
    width: 80%;
    border: 0;
    font-size: 16px;
    color: #000;
    z-index: 1000;
    height: 1000px;
    overflow: hidden;
    outline: none;
    background: transparent;
    padding: 0;
  }
  .img_hold {
    width: 200px;
    position: absolute;
    background: #fff;
    top: 0;
    right: 0;
    min-height: 100%;
    text-align: center;

    img {
      max-width: 200px;
    }
    h3 {
      font-size: 24px;
      color: #135042;
    }
  }
}
